import { Heading2, Paragraph, SectionContainer, MainLink, TextReveal } from '../Components';

const AboutShort = () => {
    return (
        <SectionContainer id="about" data-scroll-section>
            <Heading2>
                <TextReveal data-scroll>
                    Cze
                </TextReveal>
            </Heading2>
            <Paragraph>
                <TextReveal data-scroll shift={.3} transparent>
                    Nazywam si Przemysaw Majka, mieszkam w Krakowie.
                    Swoj przygod z FrontEndem rozpoczem pod koniec 2019 roku. Od tamtej pory na nauk powicam kad woln chwil i sprawia mi ona ogromn przyjemno.
                    Cigle d do poszerzania swoich kompetencji poprzez poznawanie najnowszych rozwiza i technologii.
                </TextReveal>
            </Paragraph>
            <Paragraph nice>
                <TextReveal data-scroll>
                    Poszukuj pracy jako FrontEnd Developer.
                </TextReveal>
            </Paragraph>
            <MainLink className="cursor_hover" to={"/about"} arrow="true">
                Wicej
            </MainLink>
            {/* <ImageWrapper data-scroll>
                <Image src={myFace} alt="my face" />
            </ImageWrapper> */}
        </SectionContainer>
    )
}

export default AboutShort;